import React, { Component } from "react";
import SplitText from "react-pose-text";
// import "./style_modal.scss";


class ModalPolicy extends Component {




  render() {

    const { changeStep, clickClose } = this.props;

    return (
      <div className="modal-content policy" >
        <span onClick={clickClose} className="icon_close"></span>
        <div>
          <h2>POLÍTICAS DE TRATAMIENTO DE LA INFORMACIÓN PERSONAL DE GRUPO PINEAL S.A.S</h2>
          <h3>1. GENERALIDADES </h3>
          <p>
            <span>
              De acuerdo con la definición establecida en la Ley 1581 de 2012, el dato personal es cualquier información vinculada o que pueda asociarse a una o varias personas naturales determinadas o determinables, como el nombre, la edad, el sexo, el estado civil, el domicilio, entre otros. Estos datos pueden almacenarse en cualquier soporte físico o electrónico y ser tratados de forma manual o automatizada. La Ley 1266 de 2008 define los siguientes tipos de datos de carácter personal:
          </span>
            <ul>
              <li>Dato privado: “Es el dato que por su naturaleza íntima o reservada sólo es relevante para el Titular”.</li>
              <li>Dato semiprivado: “Es semiprivado el dato que no tiene naturaleza íntima, reservada, ni pública y cuyo conocimiento o divulgación puede interesar no sólo a su Titular sino a cierto sector o grupo de personas o a la sociedad en general, como el dato financiero y crediticio de actividad comercial o de servicios a que se refiere el Título IV” de la Ley 1266.</li>
              <li>Dato público: “Es el dato calificado como tal según los mandatos de la Ley o de la Constitución Política y todos aquellos que no sean semiprivados o privados”, de conformidad con la Ley 1266 de 2008. “Son públicos, entre otros, los datos contenidos en documentos públicos, sentencias judiciales debidamente ejecutoriadas que no estén sometidos a reserva y los relativos al estado civil de las personas”. Adicionalmente, la Ley 1581 de 2012 establece las siguientes categorías especiales de datos personales:</li>
              <li>
                <span>
                  Datos sensibles: Son “aquellos que afectan la intimidad del Titular o cuyo uso indebido puede generar su discriminación, tales como aquellos que revelen el origen racial o étnico, la orientación política, las convicciones religiosas o filosóficas, la pertenencia a sindicatos, organizaciones sociales, de derechos humanos o que promueva intereses de cualquier partido político o que garanticen los derechos y garantías de partidos políticos de oposición, así como los datos relativos a la salud, a la vida sexual y los datos biométricos”. La Ley 1581 de 2012 prohíbe el tratamiento de datos sensibles con excepción de los siguientes casos:
              </span>
                <ul>
                  <li>cuando el Titular otorga su consentimiento, </li>
                  <li>el Tratamiento es necesario para salvaguardar el interés vital del Titular y éste se encuentre física o jurídicamente incapacitado, </li>
                  <li>el tratamiento es efectuado en el curso de las actividades legítimas y con las debidas garantías por parte de una fundación, ONG, asociación o cualquier otro organismo sin ánimo de lucro, cuya finalidad sea política, filosófica, religiosa o sindical, siempre que se refieran exclusivamente a sus miembros o a las personas que mantengan contactos regulares por razón de su finalidad, </li>
                  <li>el Tratamiento se refiera a datos que sean necesarios para el reconocimiento, ejercicio o defensa de un derecho en un proceso judicial, y </li>
                  <li>el Tratamiento tenga una finalidad histórica, estadística o científica, en este último caso deben adoptarse las medidas conducentes a la supresión de identidad de los Titulares.</li>

                </ul>
              </li>
              <li>Datos personales de los niños, niñas y adolescentes: Se debe tener en cuenta que aunque la Ley 1581 de 2012 prohíbe el tratamiento de los datos personales de los niños, niñas y adolescentes, salvo aquellos que por su naturaleza son públicos, la Corte Constitucional precisó que independientemente de la naturaleza del dato, se puede realizar el tratamiento de éstos “siempre y cuando el fin que se persiga con dicho tratamiento responda al interés superior de los niños, niñas y adolescentes y se asegure sin excepción alguna el respeto a sus derechos prevalentes”.</li>
            </ul>


            <span> También la ley define los siguientes roles: </span>
            <ul>
              <li>Responsable de Tratamiento: “Persona natural o jurídica, pública o privada, que por sí misma o en asocio con otros, decida sobre la base de datos y/o el Tratamiento de los datos”. GRUPO PINEAL S.A.S, de acuerdo con la ley es Responsable de Tratamiento de datos personales contenidos en sus bases de datos,</li>
              <li>Encargado del Tratamiento: “Persona natural o jurídica, pública o privada, que por sí misma o en asocio con otros, realice el Tratamiento de datos personales por cuenta del Responsable del Tratamiento”. GRUPO PINEAL S.A.S podrá realizar el tratamiento de sus datos personales a través de Encargados. Adicionalmente, para este documento se incluyen los siguientes roles.</li>
              <li>Administrador de base de datos personales: Funcionario o Encargado que tiene a cargo y realiza tratamiento a una o más bases de datos que tiene información personal.</li>
              <li>Gestor: La Oficina Asesora de Planeación llevará el control del registro de las bases de datos con información personal que hay en GRUPO PINEAL S.A.S y apoyara el ingreso de la información en el Registro Nacional de Base de Datos.</li>
            </ul>


            <h4>1.1 DISPOSICIONES GENERALES ESTABLECIDAS EN LA LEY 1581 DE 2012 PARA LA PROTECCIÓN DE DATOS PERSONALES</h4>

            <span>
              La Ley 1581 de 2012 desarrolla el derecho constitucional a conocer, actualizar y rectificar la información recogida en bases de datos y los demás derechos, libertades y garantías a que se refieren los artículos 15 y 20 de la Constitución (derecho a la intimidad y derecho a la información, respectivamente). La citada ley se aplica a los datos personales registrados en cualquier base de datos que los haga susceptibles de tratamiento por parte de entidades públicas o privadas. Considerando el modo de conservación de una base de datos, se puede distinguir entre bases de datos automatizadas y bases de datos manuales o archivos. Las bases de datos automatizadas son aquellas que se almacenan y administran con la ayuda de herramientas informáticas. Las bases de datos manuales o archivos son aquellas cuya información se encuentra organizada y almacenada de manera física, como las fichas de pedidos a proveedores que contengan información personal relativa al proveedor, como nombre, identificación, números de teléfono, correo electrónico, etc. La ley exceptúa del régimen de protección
            </span>
            <ul>
              <li>los archivos y las bases de datos pertenecientes al ámbito personal o doméstico;</li>
              <li>los que tienen por finalidad la seguridad y la defensa nacionales, la prevención, detección, monitoreo y control del lavado de activos y el financiamiento del terrorismo,</li>
              <li>los que tengan como fin y contengan información de inteligencia y contrainteligencia,</li>
              <li>los de información periodística y otros contenidos editoriales,</li>
              <li>los regulados por la Ley 1266 de 2008 (información financiera y crediticia, comercial, de servicios y proveniente de terceros países) y</li>
              <li>los regulados por la Ley 79 de 1993 (sobre censos de población y vivienda).</li>
            </ul>


            <h4>1.2 DEBERES DEL RESPONSABLE DEL TRATAMIENTO</h4>

            <span>
              El Responsable del Tratamiento ha sido definido por la Ley 1581 de 2012 como la persona natural o jurídica, pública o privada, que por sí misma o en asocio con otros decida sobre la base de datos y/o el tratamiento de los datos. La Superintendencia de Industria y Comercio, además de ser la autoridad de protección de datos personales tiene la calidad de Responsable del Tratamiento frente a las bases de datos creadas por la entidad. Son deberes de los Responsables del Tratamiento y, por consiguiente, de GRUPO PINEAL S.A.S los establecidos en el artículo 17 de la Ley 1581 de 2012:
            </span>
            <ul>
              <li>Garantizar al Titular, en todo tiempo, el pleno y efectivo ejercicio del derecho de hábeas data. </li>
              <li>Solicitar y conservar, en las condiciones previstas en la citada ley, copia de la respectiva autorización otorgada por el Titular. </li>
              <li>Informar debidamente al Titular sobre la finalidad de la recolección y los derechos que le asisten por virtud de la autorización otorgada. </li>
              <li>Conservar la información bajo las condiciones de seguridad necesarias para impedir su adulteración, pérdida, consulta, uso o acceso no autorizado o fraudulento. </li>
              <li>Garantizar que la información que se suministre al Encargado del Tratamiento sea veraz, completa, exacta, actualizada, comprobable y comprensible. </li>
              <li>Actualizar la información, comunicando de forma oportuna al Encargado del Tratamiento, todas las novedades respecto de los datos que previamente le haya suministrado y adoptar las demás medidas necesarias para que la información suministrada a éste se mantenga actualizada. </li>
              <li>Rectificar la información cuando sea incorrecta y comunicar lo pertinente al Encargado del Tratamiento. </li>
              <li>Suministrar al Encargado del Tratamiento, según el caso, únicamente datos cuyo Tratamiento esté previamente autorizado de conformidad con lo previsto en la citada ley. </li>
              <li>Exigir al Encargado del Tratamiento en todo momento, el respeto a las condiciones de seguridad y privacidad de la información del Titular. </li>
              <li>Tramitar las consultas y reclamos formulados en los términos señalados en la citada ley. </li>
              <li>Adoptar un manual interno de políticas y procedimientos para garantizar el adecuado cumplimiento de la citada ley y en especial, para la atención de consultas y reclamos. </li>
              <li>Informar al Encargado del Tratamiento cuando determinada información se encuentra en discusión por parte del Titular, una vez se haya presentado la reclamación y no haya finalizado el trámite respectivo. </li>
              <li>Informar a solicitud del Titular sobre el uso dado a sus datos. </li>
              <li>Informar a la autoridad de protección de datos cuando se presenten violaciones a los códigos de seguridad y existan riesgos en la administración de la información de los Titulares. </li>
              <li>Cumplir las instrucciones y requerimientos que imparta la Superintendencia de Industria y Comercio”.</li>
            </ul>
            <h4>1.3 DERECHOS DE LOS TITULARES</h4>

            <span>
              La Ley 1581 de 2012 establece que los Titulares de los datos personales tendrán los siguientes derechos:
          </span>
            <ul>
              <li>Conocer, actualizar y rectificar sus datos personales frente a los Responsables del Tratamiento o Encargados del Tratamiento. Este derecho se podrá ejercer, entre otros frente a datos parciales, inexactos, incompletos, fraccionados, que induzcan a error, o aquellos cuyo Tratamiento esté expresamente prohibido o no haya sido autorizado. </li>
              <li>Solicitar prueba de la autorización otorgada al Responsable del Tratamiento salvo cuando expresamente se exceptúe como requisito para el Tratamiento, de conformidad con lo previsto en el artículo 10 de la citada ley. </li>
              <li>Ser informado por el Responsable del Tratamiento o el Encargado del Tratamiento, previa solicitud, respecto del uso que le ha dado a sus datos personales. </li>
              <li>Presentar ante la Superintendencia de Industria y Comercio quejas por infracciones a lo dispuesto en la citada ley y las demás normas que la modifiquen, adicionen o complementen. </li>
              <li>Revocar la autorización y/o solicitar la supresión del dato cuando en el Tratamiento no se respeten los principios, derechos y garantías constitucionales y legales. La revocatoria y/o supresión procederá cuando la Superintendencia de Industria y Comercio haya determinado que en el Tratamiento el Responsable o Encargado han incurrido en conductas contrarias a la ley y a la Constitución. </li>
              <li>Acceder en forma gratuita a sus datos personales que hayan sido objeto de Tratamiento. Adicionalmente, el Decreto reglamentario 1377 de 2013 define que los Responsables deberán conservar prueba de la autorización otorgada por los Titulares de datos personales para el Tratamiento de los mismos.</li>
            </ul>

            <h3>2. POLÍTICAS</h3>
            <p>
              <span>Se establecen las siguientes directrices generales:</span>  <br />
              <br /><strong>Primero:</strong> Cumplir con toda la normatividad legal vigente colombiana que dicte disposiciones para la protección de datos personales.
            <br /><strong>Segundo:</strong> Cumplir con la ley de protección de datos personales de acuerdo con lo contemplado en las Políticas de Tratamiento de la Información Personal de Grupo Pineal S.A.S
            <strong>Tercero:</strong> Todas las personas que directa o indirectamente tuvieren conocimiento o pudieren conocer los datos a entregados por los usuarios, clientes, proveedores y demás, deberán tratarlos conforme las políticas internas para el tratamiento de Datos Personales.
          </p>


            <p>
              <span>Políticas específicas relacionadas con el tratamiento de Datos Personales:</span>
            </p>

            <ul>

              <li>El GRUPO PINEAL realiza el tratamiento de Datos Personales en ejercicio propio de sus funciones legales y para el efecto no requiere la autorización previa, expresa e informada del Titular. Sin embargo, cuando no corresponda a sus funciones deberá obtener la autorización por medio de un documento físico, electrónico, mensaje de datos, Internet, sitio web, o también de manera verbal o telefónica o en cualquier otro formato que permita su posterior consulta a fin de constatar de forma inequívoca que sin el consentimiento del titular los datos nunca hubieran sido capturados y almacenados en medios electrónicos o físicos. Así mismo se podrá obtener por medio de conductas claras e inequívocas del Titular que permitan concluir de una manera razonable que este otorgó su consentimiento para el manejo de sus Datos Personales.</li>
              <li>La GRUPO PINEAL S.A.S. solicitará la autorización a los Titulares de los datos personales y mantendrá las pruebas de ésta, cuando en virtud de las funciones de promoción, divulgación y capacitación, realice invitaciones a charlas, conferencias o eventos que impliquen el Tratamiento de Datos Personales con una finalidad diferente para la cual fueron recolectados inicialmente.</li>
              <li>En consecuencia, toda labor de tratamiento de Datos Personales realizada en GRUPO PINEAL S.A.S deberá corresponder al ejercicio de sus funciones legales o a las finalidades mencionadas en la autorización otorgada por el Titular, cuando la situación así lo amerite. De manera particular, las principales finalidades para el tratamiento de Datos Personales que corresponde a GRUPO PINEAL S.A.S desarrollar en ejercicio de sus funciones legales se relacionan con los siguientes tramites: · Propiedad industrial · Protección al consumidor · Control y verificación de reglamentos técnicos y metrología legal · Protección de la competencia · Vigilancia de las cámaras de comercio · Protección de datos personales · Asuntos jurisdiccionales</li>
              <li>El Dato Personal sometido a Tratamiento deberá ser veraz, completo, exacto, actualizado, comprobable y comprensible. GRUPO PINEAL S.A.S mantendrá la información bajo estas características siempre y cuando el titular informe oportunamente sus novedades.</li>
              <li>Los Datos Personales solo serán Tratados por aquellos Funcionarios de GRUPO PINEAL S.A.S que cuenten con el permiso para ello, o quienes dentro de sus funciones tengan a cargo la realización de tales actividades o por los Encargados.</li>
              <li>GRUPO PINEAL S.A.S autorizará expresamente al Administrador de las bases de datos para realizar el tratamiento solicitado por el Titular de la información.</li>
              <li>GRUPO PINEAL S.A.S no hará disponibles Datos Personales para su acceso a través de Internet u otros medios masivos de comunicación, a menos que se trate de información pública o que se establezcan medidas técnicas que permitan controlar el acceso y restringirlo solo a las personas autorizadas por ley o por el titular.</li>
              <li>Todo Dato Personal que no sea Dato Público se tratará por GRUPO PINEAL S.A.S como confidencial, aun cuando la relación contractual o el vínculo entre el Titular del Dato Personal y GRUPO PINEAL S.A.S haya finalizado. A la terminación de dicho vínculo, tales Datos Personales deben continuar siendo Tratados de acuerdo con lo dispuesto por el Manual de Archivo y Retención Documental GD01-M01.</li>
              <li>Cada área de GRUPO PINEAL S.A.S debe evaluar la pertinencia de anonimizar los actos administrativos y/o documentos de carácter público que contengan datos personales, para su publicación.</li>
              <li>El Titular, directamente o a través de las personas debidamente autorizadas, podrá consultar sus Datos Personales en todo momento y especialmente cada vez que existan modificaciones en las Políticas de Tratamiento de la información.</li>
              <li>GRUPO PINEAL S.A.S suministrará, actualizará, ratificará o suprimirá los Datos Personales a solicitud del Titular para corregir información parcial, inexacta, incompleta, fraccionada que induzca al error o aquella que haya sido tratada previa a la vigencia de la ley y que no tenga autorización o sea prohibida.</li>
              <li>Cuando le sea solicitada información, ya sea mediante una petición, consulta o reclamo por parte del Titular, sobre la manera como son utilizados sus Datos Personales, GRUPO PINEAL S.A.S deberá entregar dicha información.</li>
              <li>A solicitud del Titular y cuando no tenga ningún deber legal o contractual de permanecer en las bases de datos de GRUPO PINEAL S.A.S, los Datos Personales deberán ser eliminados. En caso de proceder una revocatoria de tipo parcial de la autorización para el Tratamiento de Datos Personales para algunas de las finalidades GRUPO PINEAL S.A.S podrá seguir utilizando los datos para las demás finalidades respecto de las cuales no proceda dicha revocatoria.</li>
              <li>Las políticas establecidas por GRUPO PINEAL S.A.S respecto al tratamiento de Datos Personales podrán ser modificadas en cualquier momento. Toda modificación se realizará con apego a la normatividad legal vigente, y las mismas entrarán en vigencia y tendrán efectos desde su publicación a través de los mecanismos dispuestos por GRUPO PINEAL S.A.S para que los titulares conozcan la política de tratamiento de la información y los cambios que se produzcan en ella.</li>
              <li>Los Datos Personales solo podrán ser tratados durante el tiempo y en la medida que la finalidad de su tratamiento lo justifique.</li>
              <li>GRUPO PINEAL S.A.S será más rigurosa en la aplicación de las políticas de tratamiento de la información cuando se trate del uso de datos personales de los niños, niñas y adolescentes asegurando la protección de sus derechos fundamentales.</li>
              <li>GRUPO PINEAL S.A.S podrá intercambiar información de Datos Personales con autoridades gubernamentales o publicas tales como autoridades administrativas, de impuestos, organismos de investigación y autoridades judiciales, cuando la soliciten en ejercicio de sus funciones.</li>
              <li>Los Datos Personales sujetos a tratamiento deberán ser manejados proveyendo para ello todas las medidas tanto humanas como técnicas para su protección, brindando la seguridad de que ésta no pueda ser copiada, adulterada, eliminada, consultada o de alguna manera utilizada sin autorización o para uso fraudulento.</li>
              <li>Cuando finalice alguna de las labores de tratamiento de Datos Personales por los Servidores, contratistas o Encargados del tratamiento, y aun después de finalizado su vínculo o relación contractual con GRUPO PINEAL S.A.S, éstos están obligados a mantener la reserva de la información de acuerdo con la normatividad vigente en la materia.</li>
              <li>GRUPO PINEAL S.A.S divulgará en sus servidores, contratistas y terceros encargados del tratamiento las obligaciones que tienen en relación con el tratamiento de Datos Personales mediante campañas y actividades de orden pedagógico.</li>
              <li>GRUPO PINEAL S.A.S no realizará transferencia de información relacionada con Datos Personales a países que no cuenten con los niveles adecuados de protección de datos, de acuerdo con los estándares que estén fijados en la misma Superintendencia.</li>
              <li>El Titular de los datos personales puede ejercer, principalmente, sus derechos mediante la presentación de consultas y reclamos ante GRUPO PINEAL S.A.S, en su sede cuyo domicilio es la calle 19 Nro. 43G – 80 , y por el correo electrónico arendon@grupopineal.com</li>
              <li>Cuando exista un Encargado del Tratamiento de Información de Datos Personales, GRUPO PINEAL S.A.S deberá garantizar que la información que le suministra sea veraz, completa, exacta, actualizada, comprobable y comprensible. Adicionalmente le comunicará de manera oportuna todas las novedades a que haya lugar para que la información siempre se mantenga actualizada.</li>
              <li>En el caso de existir un Encargado del Tratamiento de información de Datos Personales, GRUPO PINEAL S.A.S suministrará según el caso, información de Datos Personales únicamente cuyo Tratamiento realice en virtud de sus funciones legales y cuando excepcionalmente éstas no apliquen, con la autorización del Titular.</li>
              <li>GRUPO PINEAL S.A.S Informará al Encargado del Tratamiento de información de Datos Personales, de existir uno, cuando determinada información se encuentra en discusión por parte del Titular, una vez se haya presentado la reclamación y no haya finalizado el trámite respectivo.</li>
              <li>Cuando exista un Encargado del Tratamiento de información de Datos Personales, se exigirá que en todo momento, se respeten las condiciones de seguridad y confidencialidad de la información del Titular establecidas por GRUPO PINEAL S.A.S.</li>

            </ul>
            <span> DISCLAIMER DATOS PERSONALES PARA PÁGINA WEB</span>
            <span>
              Conforme a la Ley 1581 de 2012, Decreto 1074 de 2015 y a cualquier norma que los modifique, complemente, sustituya o adicione,GRUPO PINEAL S.A.S recolectará, usará, almacenará, circularán, transmitirá, transferirá y, en general, realizará el tratamiento de sus datos personales, en su calidad de Responsable del Tratamiento. El tratamiento de sus datos personales se realizará para (i) mejorar nuestros productos y servicios; (ii) Tratar o procesar sus solicitudes; (iii) Realizar controles; (iv) Realizar programas y acciones de atención al cliente; (v) publicidad, marketing y estudios de opinión; (vi) ofrecer productos, servicios, descuentos, promociones, eventos, sorteos, entre otros; (vii) Enriquecimiento de nuestros datos; (viii) perfilamiento de nuestros clientes y visitantes; (ix) análisis de rendimiento; y (x) en general, desarrollar la actividad comercial de GRUPO PINEAL S.A.S.
          </span>

            <span>Como titular de sus datos personales Usted tiene derecho a: </span>
            <ul>

              <li>Acceder en forma gratuita a los datos proporcionados que hayan sido objeto de tratamiento. </li>
              <li>Conocer, actualizar y rectificar su información frente a datos parciales, inexactos, incompletos, fraccionados, que induzcan a error, o a aquellos cuyo tratamiento esté prohibido o no haya sido autorizado. </li>
              <li>Solicitar prueba de la autorización otorgada. </li>
              <li>Presentar ante la Superintendencia de Industria y Comercio quejas por infracciones a lo dispuesto en la normatividad vigente. </li>
              <li>Revocar la autorización y/o solicitar la supresión del dato, siempre que no exista un deber legal o contractual que impida eliminarlos. </li>
              <li>Abstenerse de responder las preguntas sobre datos sensibles. Los titulares de datos personales pueden acceder a nuestra Política de Tratamiento, la cual se encuentra publicada en nuestra página web en el siguiente enlace www.paissano.app y podrán ponerse en contacto con GRUPO PINEAL S.A.S. por correo electrónico escribiendo a la siguiente dirección electrónica: arendon@grupopineal.com</li>

            </ul>
          </p>
        </div>

        <div className="content-button">
          <a onClick={() => changeStep(0)}>Atras</a>
        </div>
      </div>
    );
  }
}

export default ModalPolicy;
